@import url(https://fonts.googleapis.com/css2?family=Bree+Serif&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@font-face {
  font-family: serifme;
  src: url(/weather-web-app/static/media/PlayfairDisplay-BlackItalic.696a029d.otf);
}

@font-face {
  font-family: barlow;
  src: url(/weather-web-app/static/media/Barlow-Thin.31c931de.ttf);
}

@font-face {
  font-family: nexa;
  src: url("/weather-web-app/static/media/Nexa Light.2bac835e.otf");
}

* {
  /* color: #fcf80a; */
 
  font-family: barlow;
  text-transform: capitalize;
  
}

.containers {
  width: 100%;
  background-image: url(/weather-web-app/static/media/jonathan-bowers-BqKdvJ8a5TI-unsplash.8dd1dd86.jpg);
  background-size: cover;
  background-repeat: no-repeat;
}

input {
  border-bottom: 1px solid #fff !important;
  background-color: transparent;
  border-top-style: hidden;
  border-right-style: hidden;
  border-left-style: hidden;
  border-bottom-style: groove;
  outline: none;
  color: #fff;
  font-size: 1.5rem;
  
}
.myseacrhBtn{
  width:50px;
  height:50px;
  background-color: transparent;
  outline: transparent;
  border: 1px solid #fff;
  border-radius: 50%;
  color:#fff;
  position: relative;
  top: -5px;
  left: 20px;
  transition: 0.3s;
}
.myseacrhBtn:hover{
  background-color: rgba(255, 255, 255, 0.3);
  color: #000;
  -webkit-backdrop-filter: 10px;
          backdrop-filter: 10px;

}
.fontSize1 {
  font-size: 1.3rem;
}
.fontSize1-2{
  font-size:1.5rem;
}
.fontSize2 {
  font-size: 3rem;
}
.fontSize3 {
  font-size: 5rem;
}
.fontWhite {
  color: #fff;
}
.fontWhiteRgba {
  color: rgba(255, 255, 255, 0.4);
}
.searchDivHeight {
  height: 28vh;
  color: #fff;
}

.divHeightLocation{
  height: 72vh;
  
}
.location { color: #fff;
  height: 300px;
}
.time {
  position: absolute;
  right: 40px;
  font-weight:900 ;
}
.roundMainDiv {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 70vh;
  color:#b8f5ea ;
  padding:0px 80px 50px 50px;
}
.roundDiv {
  width: 280px;
  height: 280px;
  border-top: 2px solid rgba(255, 255, 255, 0.1);
  border-radius: 100%;
  background-color: rgba(255,255,255,0.1);
  -webkit-backdrop-filter: blur(20px);
          backdrop-filter: blur(20px);
}
.roundInnerDit {
  width: 280px;
  height: 280px;
}
.tempText {
  margin: 20px 0 0 0;
}
.tempTextRightDiv {
  margin: 0 0 0 0;
}
hr {
  width: 80%;
  margin-left:10% ;
  margin-top: 0px;
  padding-top: 0px;
  margin-bottom: 12px;
  padding-bottom: 0px;
}
.HumidityDot {
  width: 10px;
  height: 10px;
  background-color: rgb(11, 245, 11);
  border-radius: 100%;
  display: inline-block;
  position: relative;
  top: -13px;
}
.mainrightDiv{
  display: flex;
  justify-content: center;
  align-items: center;
}
.rightMainDiv {
  width: 100%;
  height: 100%;
  color: #fff;
  border-top-left-radius: 25px;
  border-top: 1.6px solid rgba(255, 255, 255, 0.1);
  background-color: rgba(255, 255, 255, 0.1);
  -webkit-backdrop-filter: blur(20px);
          backdrop-filter: blur(20px);
}
.bgInput {
  background-color: royalblue;
}
.bgContry {
  background-color: silver;
}
.bgRound {
  background-color: slateblue;
}
.bgRight {
  background-color: silver;
}


/* midea querys */
@media only screen and (max-width: 768px) {
  .divHeightLocation{
    height: 30vh;
  }
  .roundMainDiv {
  width: 100%;
    height: 60vh;
  }
  .rightMainDiv {
    height: auto;
  border-top-right-radius: 25px;
  border-top-left-radius: 25px;
  }
  .mainrightDiv{
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .allDivHeight {
    height: auto;
    position: static;
  }
}
@media only screen and (max-width: 600px) {
  
  .time {
 position: absolute;
 right: 40px;
 top: 80px;
  }
  
}


